//fonts
@import url('../css/uikit.min.css');
@import url('../css/background.css');
@import url('https://fonts.googleapis.com/css?family=Lato:400,700,700i,900,900i|Open+Sans:400,700,700i,800,800i|Playfair+Display:400,700,700i,900,900i'); //colors
$white: #FFFFFF;
$white-20: rgba(255,255,255,.20);
$grey: #E5ECEF;
$blue: #22E8F1; //icons
$dark_blue: #014165;
$dark_blue-50: rgba(1,65,101,.5);

.icon {
  display: inline-block;

  &:before {
    content: "";
    width: inherit;
    height: inherit;
    position: absolute;
    display: block;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }

  &.icon-hanburger {
    &:before {
      background-image: url("../img/icon/hanburger.svg");
    }
  }

  &.icon-check {
    &:before {
      background-image: url("../img/icon/check.svg");
    }
  }

  &.icon-check-blue {
    &:before {
      background-image: url("../img/icon/check-blue.svg");
    }
  }

  &.icon-drop {
    &:before {
      background-image: url("../img/icon/drop.svg");
    }
  }

  &.icon-ruby {
    &:before {
      background-image: url("../img/icon/ruby.svg");
    }
  }

  &.icon-leaf {
    &:before {
      background-image: url("../img/icon/leaf.svg");
    }
  }

  &.icon-time{
    &:before {
      background-image: url("../img/icon/time.svg")
    }
  }

  &.icon-size {
    &:before {
      background-image: url("../img/icon/size.svg");
    }
  }
}
