body {
  font-family: 'Open Sans', sans-serif;
}

section {
  position: relative;
}

.wrapper {
  position: relative;
  overflow: hidden;
}
  .button,
  a.button {
    color: $dark_blue;
    text-align: center;
    padding: 13px 56px;
    cursor: pointer;
    transition: 0.6s;
    text-decoration: none;
    &:hover {
      background: $dark_blue;
      color: $white;
      border-color: $dark_blue !important;
    }
  
    &.to_form,
    &.js_submit {
      font-size: 20px;
      border-radius: 25px;
      border: 2px solid;
      text-transform: uppercase;
    }
  }
.uk-slidenav {
  polyline{
    color: #3b708b!important;
  }
}

.section-1 {
  position: relative;
  height: 1080px;
  background: linear-gradient(180deg, #00639B 0%, rgba(0, 99, 155, 0) 100%, rgba(0, 99, 155, 0) 100%) fixed;
  padding-top: 47px;
  // padding-bottom: 195px;

  .top_panel {
    margin-bottom: 253px;

    .icon-hanburger {
      width: 18px;
      height: 14px;
      border: 2px solid $white-20;
      border-radius: 150px;
      padding: 13px 11px;
      color: $white;
    }

    .logo {
      color: $white;
      font-size: 20px;
      text-transform: uppercase;
      padding-bottom: 7px;
      border-bottom: 2px solid $white-20;

      span {
        font-weight: 700;
      }
    }
  }

  .icon-check {
    width: 16px;
    height: 12px;
    padding: 14px 12px;
    border: 2px solid $blue;
    border-radius: 150px;
    color: $white;
  }

  .vitagem_title {
    font-family: 'Playfair Display', serif;
    color: $white;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
    font-size: 30px;
    letter-spacing: 0.1em;

    span {
      color: $blue;
      text-decoration: underline;
    }
  }

  .list {
    .item {
      > div {
        padding-bottom: 20px;
      }

      span {
        font-weight: normal;
        line-height: 31px;
        font-size: 16px;
        color: #FFFFFF;
        padding-left: 10px;
      }
    }
  }

  .old_price {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: normal;
    line-height: 26px;
    font-size: 16px;
    color: $dark_blue;
    padding-top: 50px;

    .price {
      text-transform: uppercase;
      font-size: 20px;
    }
  }

  .new_price {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 100;
    font-size: 40px;
    color: $dark_blue;
  }
}

.section-2 {
  // padding-top: 250px;
  .video {
    background: linear-gradient(180deg, #003D5F 0%, rgba(255, 255, 255, 0) 50%, #003D5F 100%);
