}

.section-3,
.section-4 {
  .card {
    min-height: 470px;
    position: relative;
    overflow: hidden;
    background: $white;
    box-shadow: 0px 25px 45px rgba(0, 61, 95, 0.5);
    .layer {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;

      .vitagem_text {
        font-family: 'Lato', sans-serif;
        text-align: center;
        font-style: normal;
        font-weight: 900;
        line-height: normal;
        font-size: 160px;
        letter-spacing: 0.31em;
        text-transform: uppercase;
        background: -webkit-linear-gradient(#CCD8DF,#F2F5F7);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        z-index: 0;
        span{
          font-weight: 300;
        }
      }

      .pack {
        position: absolute;
        width: 100%;
        height: 752px;
        top: 40px;
        background-image: url("../img/pack.png");
        background-size: contain;
        background-position: bottom center;
        background-repeat: no-repeat;
      }
    }

    .title {
      color: $dark_blue;

      h2 {
        font-family: 'Playfair Display', serif;
        color: $dark_blue;
        text-transform: uppercase;
        font-size: 20px;
        font-weight: bold;
      }
    }

    .text {
      color: $dark_blue;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 28px;
    }
  }
}

