    .tradition-content {
      .scientist {
        .card-wrap {
          position: relative;

          .card {
            position: relative;
            z-index: 2;
            height: 295px;

            &.rail {
              // background: url('../img/left-rail.png');
              // background-position: right;
              // background-repeat: no-repeat;
              // background-size: contain;
              &::before {
                content: '';
                display: block;
                position: absolute;
                width: 24px;
                z-index: 1;
              }

              &.left {
                &::before {
                  height: calc(75% + 60px);
                  border-top: 4px solid #fff;
                  border-left: 4px solid #fff;
                  border-top-left-radius: 50px;
                  top: 50%;
                  left: -28px;
                }
              }

              &.center {
                &::before {
                  height: 122px;
                  border-left: 4px solid #fff;
                  top: 100%;
                  left: calc(50% - 2px);
                }
              }

              &.right {
                &::before {
                  height: calc(68% + 60px);
                  border-top: 4px solid #fff;
                  border-right: 4px solid #fff;
                  border-top-right-radius: 50px;
                  top: 50%;
                  right: -27px;
                }
              }
            }
            background: $white;
            padding: 30px 25px;
            margin-top: 85px;
            border-radius: 10px;
            margin-bottom: 120px;
          }

          .mineral {
            display: block;
            position: absolute;
            bottom: 85px;
            left: -70px;
            width: 273.08px;
            height: 276.7px;
            z-index: 1;

            &._1 {
              background: url("../img/mineral.png");
              background-position: center;
              background-size: contain;
              background-repeat: no-repeat;
            }

            &._2 {
              background: url("../img/mineral_2.png");
              background-position: center;
              background-size: contain;
              background-repeat: no-repeat;
            }

            &._3 {
              background: url("../img/mineral_3.png");
              background-position: center;
              background-size: contain;
              background-repeat: no-repeat;
            }
          }
        }

        .text {
          h2 {
            font-family: 'Playfair Display', serif;
            color: $dark_blue;
            line-height: normal;
            font-size: 20px;
            font-weight: 900;
          }

          p {
            color: $dark_blue;
            line-height: 26px;
            font-size: 16px;
          }
        }
      }

      .switch {
        .items {
          position: relative;
          z-index: 2;

          .item {
            &.uk-active {
              a {
                background: $white;
              }
            }

            a {
              background: $dark_blue;
              border: 4px solid $blue;
              border-radius: 150px;
              width: 20px;
              height: 20px;
            }
          }
        }
      }
    }

    .scale {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 24px;
      left: 0;
      bottom: 32px;
      background: url("../img/scale.png");
      background-position: center;
      background-repeat: no-repeat;
      background-size: 1638px;
      z-index: 1;
    }
  }

  .main-title {
    padding-top: 0;
    margin-bottom: 166px;

    h2 {
      font-family: 'Lato', sans-serif;
      font-style: normal;
      font-weight: 900;
      line-height: normal;
      font-size: 97px;
