    background-size: contain;
    // margin-top: -70px;
    z-index: 1;
  }

  .content-text {
    padding-top: 95px;
    // padding-left: 120px;
    p {
      font-style: normal;
      font-weight: normal;
      line-height: 26px;
      font-size: 16px;
      color: $white;
    }
  }
}

